  newState() {
    this.setState({
      data: <div>
        {this.state.statetopic.map(eachtopic => {
          var questions = eachtopic.questions.map((anyques) => {
            return <tr>
              <td>Question</td>
              <td>{anyques.question}</td>
            </tr>

          })
          return <div><div className="card" style={{ backgroundColor: "white" }}> <table>
            <tr style={{ backgroundColor: "grey" }}>
              <td>Name of the topic</td>
              <td>{eachtopic.topic}</td>
            </tr>
            {questions}
          </table>
          </div>
            <br />
          </div>
        })}
      </div>
    })
  }
